import {Component, OnInit, ViewChild} from '@angular/core';
import {MatDialog, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {DialogComponent} from './dialog/dialog.component';
import {Sheet321Service} from './sheet321.service';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import {ConfirmationDialogComponent} from './confirmation-dialog/confirmation-dialog.component';
import { NotifierService } from 'src/app/notifier.service';


// @ts-ignore
@Component({
  selector: 'app-sheet321',
  templateUrl: './sheet321.component.html',
  styleUrls: ['./sheet321.component.css']
})
export class Sheet321Component implements OnInit {

  dataSource: MatTableDataSource<any>;
  displayedColumns: string[] = ['id', 'bankName', 'bankCode', 'typeOfPlacements',
  'accountNumber', 'amount', 'amount2', 'tenor', 'effectiveDate', 'maturityDate', 'interestRate',
    'upfrontInterestReceived', 'accruedInterestReceivable', 'remarks'];
  confirmDelete: boolean;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(private dialog: MatDialog,
              private sheet321Service: Sheet321Service,
              private notifierService: NotifierService) { }

  ngOnInit(): void {
    this.getAllMDFIR321Data();
  }

  // tslint:disable-next-line:typedef

  openDialog(): void {
    this.dialog.open(DialogComponent, {
       width: '30%',
    }).afterClosed().subscribe(value => {
      if (value === 'save'){this.getAllMDFIR321Data();
      }
    });
  }

  getAllMDFIR321Data(): void {
    this.sheet321Service.getMDFIR321Data().subscribe({
      next: (res) => {
        console.log(res.sheet321);
        this.dataSource = new MatTableDataSource(res.sheet321);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }, error: () => {console.log('Error while fetching data');
      }
    });
  }

  // tslint:disable-next-line:typedef
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  editReportRow(row: any): void {
    this.dialog.open(DialogComponent, {width: '30%', data: row}).afterClosed().subscribe(value => {
      if (value === 'update'){
        this.getAllMDFIR321Data();
      }
    });
  }

  deleteReportRow(id: any): void {
    this.sheet321Service.deleteMDFIR321Data(id).subscribe({
      next: value => {
        this.notifierService.showNotification('Deleted successfully', 'OK', 'success');
        this.getAllMDFIR321Data();
      }, error: err => {
        this.notifierService.showNotification('Unable to delete', 'OK', 'error');
      }
    });
  }

  // tslint:disable-next-line:typedef
  openConfirmationDialog(id) {

    this.dialog.open(ConfirmationDialogComponent, {
      width: '17%',
      data: true
    }).afterClosed().subscribe(value => {
      if (value === 'delete'){
        this.deleteReportRow(id);
        this.getAllMDFIR321Data();
      }
    });
  }
}
